var _this, countDown;
/**
 *  简单封装了下， 应用范围比较狭窄，可以在此基础上进行扩展使用
 *  比如加入image， iconSize可控等
 */
export default {
    data() {
        return {
            showPassword: false, //是否显示明文
            second: 0, //倒计时
            isRunCode: false, //是否开始倒计时
            typeList: {
                left: 'icon-zuo',
                right: 'icon-you',
                up: 'icon-shang',
                down: 'icon-xia'
            },
        }
    },
    props: {
        readOnly: {
            //是否显示获取验证码（二选一）
            type: [Boolean, String],
            default: false,
        },
        type: String, //类型
        logo: String, //类型
        value: String, //值
        placeholder: String, //框内提示
        isShowCode: {
            //是否显示获取验证码（二选一）
            type: [Boolean, String],
            default: false,
        },
        codeText: {
            type: String,
            default: "获取验证码",
        },
        setTime: {
            //倒计时时间设置
            type: [Number, String],
            default: 60,
        },
        maxlength: {
            //最大长度
            type: [Number, String],
            default: 30,
        },
        isShowPass: {
            //是否显示密码图标（二选一）
            type: [Boolean, String],
            default: false,
        },
        icon: {
            type: String,
            default: ''
        },
        title: {
            type: String,
            default: '标题'
        },
        tips: {
            type: String,
            default: ''
        },
        navigateType: {
            type: String,
            default: 'right'
        },
        border: {
            type: String,
            default: 'b-b'
        },
        hoverClass: {
            type: String,
            default: 'cell-hover'
        },
        iconColor: {
            type: String,
            default: '#333'
        }
    },
    mounted() {
        _this = this
        //准备触发
        this.$on('runCodes', (val) => {
            this.runCodes(val);
        });
        clearInterval(countDown);//先清理一次循环，避免缓存
    },
    methods: {
        showPass() {
            //是否显示密码
            this.showPassword = !this.showPassword
        },
        onInput(e) {
            //传出值
            this.$emit('input', e.target.value)
        },
        setCode() {
            //设置获取验证码的事件
            if (this.isRunCode) {
                //判断是否开始倒计时，避免重复点击
                return false;
            }
            this.$emit('setCode')
        },
        runCodes(val) {
            console.error("runCodes")
            //开始倒计时
            if (String(val) == "0") {

                //判断是否需要终止循环
                this.second = 0; //初始倒计时
                clearInterval(countDown);//清理循环
                this.isRunCode = false; //关闭循环状态
                return false;
            }
            if (this.isRunCode) {
                //判断是否开始倒计时，避免重复点击
                return false;
            }
            this.isRunCode = true
            this.second = this._setTime //倒数秒数

            let _this = this;
            countDown = setInterval(function () {
                _this.second--
                if (_this.second == 0) {
                    _this.isRunCode = false
                    clearInterval(countDown)
                }
            }, 1000)
        }


    },
    computed: {
        _type() {
            //处理值
            const type = this.type
            return type == 'password' ? 'text' : type
        },
        _isShowPass() {
            //处理值
            return String(this.isShowPass) !== 'false'
        },
        _isShowCode() {
            //处理值
            return String(this.isShowCode) !== 'false'
        },
        _setTime() {
            //处理值
            const setTime = Number(this.setTime)
            return setTime > 0 ? setTime : 60
        },
        getVerCodeSecond() {
            //验证码倒计时计算
            if (this.second <= 0) {
                return this.codeText;
            } else {
                if (this.second < 10) {
                    return '0' + this.second + "s";
                } else {
                    return this.second + "s";
                }
            }

        }
    }
}
